import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-testcompo',
  templateUrl: './testcompo.component.html',
  styleUrls: ['./testcompo.component.css']
})
export class TestcompoComponent implements OnInit {
  name = "Nafeez Pasha";
  redtxt = "red";
  times = 0;
  constructor() { }
  show : boolean = false;
  size : number = 30;
  bgClr = "";

  ngOnInit() {
  }

  changeColor(val){
    this.bgClr = val;
    console.log(this.bgClr)
  }

  countClick(){
  	this.show = !this.show;
  }

}



